/**
 * Imports
 */
import { toast as notify } from 'bulma-toast'

import {
    jarallax
} from 'jarallax'
const feather = require('feather-icons')
require('console-success')
import Bulma from '@vizuaalog/bulmajs'

/**
 * Passport
 */

require('dotenv').config()

const passport = {
    secret: process.env.SECRET_KEY,
    url: process.env.DB_URL
}

/**
 * Jarallax
 */
jarallax(document.querySelectorAll('.jarallax'), {
    speed: 0.2
})

/**
 * Register 
 */

const addUser = (name, email, phone, password) => {
    document.querySelector('#submit').classList.toggle('is-loading')
    console.log(name, email, phone, password)
    fetch(passport.url + '/users', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': passport.secret
            },
            body: JSON.stringify({
                name,
                email,
                phone,
                password
            }),
            cache: 'no-cache'
        })
        .then(function (response) {
            return response.json()
        })
        .then(function (data) {
            document.querySelector('#submit').classList.toggle('is-loading')
            console.log('data = ', data)
        })
        .catch(function (err) {
            console.error(err)
        })

}

const authUser = (email, password) => {
    document.querySelector('#auth').classList.toggle('is-loading')
    console.log(email, password)
    fetch(passport.url + '/users/auth/', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': passport.secret
            },
            body: JSON.stringify({
                email,
                password
            }),
            cache: 'no-cache'
        })
        .then(function (response) {
            return response.json()
        })
        .then(function (data) {
            setTimeout(() => {
                notify({
                    message: 'Has iniciado sesin correctamente. ',
                    type: 'is-success',
                    dismissible: true,
                    pauseOnHover: true,
                })
                document.querySelector('#auth').classList.toggle('is-loading')
            }, 1000);
            
            console.log('data = ', data)
        })
        .catch(function (err) {
            console.error(err)
        })

}

const register = document.forms.register

if(register)
register.onsubmit = (e) => {
    e.preventDefault()
    console.log(register)
    addUser(
        register.name.value,
        register.email.value,
        register.phone.value,
        register.password.value,
    )
}

const authenticate = document.forms.authenticate

if(authenticate)
authenticate.onsubmit = (e) => {
    e.preventDefault()
    console.log(authenticate)
    authUser(
        authenticate.email.value,
        authenticate.password.value,
    )
}

document.querySelector('.passport-trigger')?.addEventListener('click', (e) =>{
    e.preventDefault()
    document.querySelector('.passport-container').classList.toggle('is-hidden')
})

/**
 * Welcomming the advanced user.
 */
feather.replace()
console.success('Bienvenido a Intraders Passport. Autentcate con seguridad.')